body{margin:0;padding:0;font-family:"Courier New",Courier,monospace}.main{display:flex;width:100%;min-height:100vh}.main section{width:50%;padding:.5rem;box-sizing:border-box}.main .editor{background-color:#ddd;color:#333;position:relative}.main .editor #input{border:0;background-color:transparent;font-size:12px;font-family:"Courier New",Courier,monospace;resize:none;width:100%;min-height:5em;line-height:1.3}.main .console{background-color:#333;color:#ddd}.main .console #output{background-color:transparent;font-size:12px;font-family:"Courier New",Courier,monospace;width:100%;border:0;white-space:pre;line-height:1.3}.btn-run{position:fixed;bottom:1.5rem;right:2rem;background-color:#ddd;color:#333;border:1px solid #333;padding:.4rem .8rem;text-align:center;text-decoration:none;display:inline-block;font-size:1.2rem;border-radius:16px;cursor:pointer;font-weight:bold}.btn-run:hover{background-color:#333;color:#ddd;border-color:#ddd}.btn-run:active{background-color:#ddd;color:#333;border-color:#333}@media (max-width: 575.98px){.main{flex-direction:column}.main section{width:100%;min-height:50vh}.btn-run{bottom:1rem;right:1rem}}
